import Vue from 'vue'
import VueRouter from 'vue-router'
import Huawei from '../views/Huawei.vue'
import mi from '../views/mi.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Huawei',
    component: Huawei
  },
  {
    path: '/mi',
    name: 'mi',
    component: mi
  }

]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
